.classList {
    padding: 20px;
    border: 10px solid white;
    background-color: #a9becc;
 	width: 60em;
 	text-align: center;
 	border-radius: 15px;
 	transition: all 0.3s;
 
}

.classUL {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	list-style-type: none;
	
}

.classList:hover{
	background-color: #7d9aad;
	transform: scale(1.02);

}



